@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
@import url("https://fonts.googleapis.com/css?family=Dokdo|Montserrat|Roboto");

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.5 rem;
}

.hero {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  padding: 20px;
  background: url("https://images.unsplash.com/photo-1497409988347-cbfaac2f0b12?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80");
  background-size: cover;
  margin-bottom: 16px;

  @media only screen and (max-width: 864px) {
    min-height: 50vh;
    flex-direction: column;
  }
}

.avatar {
  margin: 50px;
}

.progressBar {
  padding: 2px;
  border-radius: 5px;
}
